import { Component, OnInit, ViewChild } from '@angular/core';
import {NgForm} from '@angular/forms';
import { Image } from '../shared/image.model';
import {ImageService} from '../shared/image.service';
import { Observable} from 'rxjs';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.css']
})
export class IndexComponent implements OnInit {
   @ViewChild('searchForm') private searchForm: NgForm;
   imageList$: Observable<Image[]>;
   imageList: Image[];
  constructor(private _imageservice: ImageService) { }

  ngOnInit() {
  }

   getPhotos() {
    this.imageList = [];
   // thi(s._imageservice.getImageList().subscribe(data => this.imageList = data);
    const searchkey = this.searchForm.value.searchTerm ? this.searchForm.value.searchTerm : '';
   this.imageList$ =  this._imageservice.getImageList(searchkey);
   }
}
