import { Component, OnInit, Input , Inject , ViewChild, ElementRef, Renderer2} from '@angular/core';
import {LOCAL_STORAGE, WebStorageService} from 'angular-webstorage-service';
import { Image } from '../shared/image.model';
import { ImageService } from '../shared/image.service';

@Component({
  selector: 'app-image-tile',
  templateUrl: './image-tile.component.html',
  styleUrls: ['./image-tile.component.css']
})
export class ImageTileComponent implements OnInit {

  @Input() private image: Image;
  private likedImage: any = [];
  private isLikedImage: Boolean = false;
  private isZoomin: Boolean = true;
  @ViewChild('imageHolder') imageHolder : ElementRef;

  constructor(private _imageService: ImageService, @Inject(LOCAL_STORAGE) private storage: WebStorageService, private renderer : Renderer2) { }

  ngOnInit() {
    const favImageList = this.storage.get('likedImage') ? (String(this.storage.get('likedImage'))).split(',') : [];
    const index = favImageList.indexOf(String(this.image.id));
   if (index > -1) {
      this.isLikedImage = true;
    } else {
     this.isLikedImage = false;
    }
  }

  likeImage() {
    const favImageList = this.storage.get('likedImage') ? String((this.storage.get('likedImage'))).split(',') : [];
    console.log('fav-image-list');
    console.log(favImageList);
    const index = favImageList.indexOf(String(this.image.id));
    if (index > -1) {
      favImageList.splice(index, 1);
      this.isLikedImage = false;
    } else {
      favImageList.push(String((this.image.id)));
      this.isLikedImage = true;
    }
    this.storage.set('likedImage', favImageList);
   }

   zoomAction() {
    this.isZoomin = !this.isZoomin;
   }

   onHovering() {
      this.renderer.addClass(this.imageHolder.nativeElement , 'image-holder-hover');
   }
   onUnovering() {
    this.renderer.removeClass(this.imageHolder.nativeElement , 'image-holder-hover');
   }
 }
